<template>
  <div class="caixi">
    <div class="list" v-for="(item, index) in caixiData" :key="index">
      <div class="item">
        <div class="title-box">
          <div class="name">{{ item.name }}<van-icon name="arrow" /></div>
          <div class="img-box">
            <img class="auto-img" src="" alt="" />
          </div>
        </div>
      </div>
      <div class="dish-box">
        <div
          class="dish"
          @click="searchDish(dishItem.name)"
          v-for="(dishItem, dishIndex) in item.cs"
          :key="dishIndex"
        >
          {{ dishItem.name }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Caixi',
  data() {
    return {
      caixiData: [],
    }
  },
  created() {
    this.getCaixiData()
  },
  methods: {
    getCaixiData() {
      this.axios({
        method: 'get',
        url: 'https://apis.netstart.cn/douguo/recipe/flatcatalogs',
      })
        .then((result) => {
          if (result.status === 200) {
            this.caixiData = result.data.result.cs[6].cs
          }
        })
        .catch((err) => {})
    },
    searchDish(value) {
      this.$router.push({ path: 'search', query: { value } })
    },
  },
}
</script>

<style lang="less" scoped>
.caixi {
  .list {
    .item {
      margin: 10px 15px;
      width: calc(100% -30px);
      height: 50px;
      opacity: 0.7;
      background-color: #facc30;
      color: #2d2d2d;
      border-radius: 5px;
      .name {
        line-height: 50px;
        text-align: center;
        font-size: 15px;
        font-weight: bold;
      }
    }
    .dish-box {
      padding: 0 10px;
      display: flex;
      flex-wrap: wrap;
      .dish {
        width: 78px;
        margin: 5px 5px;
        line-height: 30px;
        text-align: center;
        border: 1px solid #000;
        border-radius: 5px;
      }
    }
  }
}
</style>
